.web-title {
  font-family: 'Pacifico', cursive;
}

.reg-form-bg {
  background: url(/static/media/register-bg.9b63e88b.jpg) no-repeat;
  background-size: cover;
  min-height: 100vh;
}

.reg-form-overlay {
  background-color: rgba(0, 0, 0, 0.65);
  min-height: 100vh;
  padding: 10rem 0;
}

.reg-form {
  background-color: rgba(255, 255, 255, 0.75);
  border-radius: 0.75rem;
  height: 100%;
}



